import GameStage from "@/stages/GameStage"
import ChatMessage from "@/ChatMessage"
import DialogueNode from "@/DialogueNode"
import GameButton from "@/GameButton"

export default class DialogueTree {

  private stage: GameStage
  private nodes: { [id: string]: DialogueNode[]} = {}
  private activeNode: DialogueNode | undefined

  constructor(stage: GameStage, nodes: { [id: string]: DialogueNode[]}) {
    this.stage = stage
    this.nodes = nodes
  }

  get currentMessage(): ChatMessage {
    return (this.activeNode) ? this.activeNode.msg : new ChatMessage("", "Error: Dialogue tree not initialized.")
  }

  get options(): DialogueNode[] {
    return (this.activeNode) ? this.activeNode.options : []
  }

  getOptionButtons(options: DialogueNode[]): GameButton[] {
    let buttons: GameButton[] = []

    for (let option of options) {
      buttons.push(new GameButton(option.title, () => {
        console.log("Pressed button", option)
        this.select(option)
      }))
    }

    this.stage.buttons = buttons
    return buttons
  }

  select(option: DialogueNode) {
    if (option.endpoint == "") {
      this.goto(option.id.id, option.id.index)
    } else {
      this.stage.endpoints[option.endpoint]()
    }
  }

  private goto(id: string, index: number) {
    let node = this.nodes[id][index]
    if (node == undefined) {
      console.error(`Could not find dialogue node with id ${id}.${index}`)
      return
    }

    this.activeNode = node
    this.getOptionButtons(node.options)
    this.stage.sendMessage(this.currentMessage)
    //console.log("Went to", id, index, this.currentMessage.text)
  }

  static parseDialogue(state: any, stage: GameStage): DialogueTree {
    state = state["introStage"]

    // Create a map of all nodes in this tree
    let nodes: { [id: string]: DialogueNode[]} = {}
    for (let node of state.nodes) {
      let id = node.id
      let title = (node.title) ? node.title : "Next"

      // One node may have many lines of text.
      let messages = (node.text) ? node.text.split(/\n\n/)
        .filter((line: string) => { return line.length > 0 })
        .map((line: string) => ChatMessage.parseLine(line))
        : []

      // Each line should be used to create a separate node
      nodes[id] = []
      for (let [i, msg] of messages.entries()) {
        let node = new DialogueNode({ id: id, index: i }, stage, msg, (i == 0) ? title : "Next")
        nodes[id][i] = node

        // Link the last node to this one. The very last node will not be linked here, but have options set later.
        if (i >= 1) nodes[id][i - 1].addOption(node)
      }

      // Some nodes don't have any text, so create at least one node for each node
      if (nodes[id].length == 0) nodes[id][0] = new DialogueNode({ id: id, index: 0 }, stage, new ChatMessage(), title)
    }

    // Create links between nodes with completely different ids
    for (let node of state.nodes) {
      let options = (node.options) ? node.options : []
      let endpoint = (node.endpoint) ? node.endpoint : ""

      let subnodes = nodes[node.id]
      let lastNode = subnodes[subnodes.length - 1]

      for (let optionId of options) {
        // The option entry point is always the first subnode
        lastNode.addOption(nodes[optionId][0])
      }
      lastNode.endpoint = endpoint
    }

    let tree = new DialogueTree(stage, nodes)
    // Set our active node as the first node in the parsed state
    tree.goto(state.nodes[0].id, 0)

    return tree
  }

}
